<template>
    
    <tituloBusqueda titulo="Alta de Plaza" descripcion="Da de alta la plaza para poder ser asiganada a maestro"></tituloBusqueda>

    <div class="formularios">

        <div class="datos-banco">
            <article>DATOS GENERALES</article>

        </div>

        <div class="row">
            <div class="col-md-4">
                <input_seccion @seccion_val="(n)=>seccion=n"></input_seccion>
            </div>
            <div class="col-md-4">
                <div class="form-group">
                    <input type="text" v-model="codigo" @keypress="validaCodigo" class="form-control" aria-describedby="curp"
                        placeholder="CODIGO">
                </div>
            </div>
            <div class="col-md-4">
                <div class="form-group">
                    <div class="slc">
                        <select id="select-model" v-model="categoria">
                            <option value=null>CATEGORIA</option>
                            <option value="A01035">A01035</option>
                            <option value="A01803">A01803</option>
                            <option value="A01805">A01805</option>
                            <option value="A01806">A01806</option>
                            <option value="A01807">A01807</option>
                            <option value="A01820">A01820</option>
                            <option value="A03803">A03803</option>
                            <option value="A05009">A05009</option>
                            <option value="A08006">A08006</option>
                        </select>
                    </div>
                </div>
            </div>
        </div>

        <div class="row">
            <div class="col-md-4 ">
                <div class="form-group ">
                    <div class="slc">
                        <select id="select-model">
                            <option value="0">NIVEL</option>
                            <option value="1">NIVEL 1</option>
                            <option value="2">NIVEL 2</option>
                            <option value="3">NIVEL 3</option>
                        </select>
                    </div>
                </div>
            </div>
            <div class="col-md-4 ">
                <div class="form-group ">
                    <div class="slc">
                        <select id="select-model">
                            <option value="0">ZONA</option>
                            <option value="1">ZONA 1</option>
                            <option value="2">ZONA 2</option>
                            <option value="3">ZONA 3</option>
                        </select>
                    </div>
                </div>
            </div>
        </div>

        <div class="row">
            <div class="col-md-4 ">
                <div class="form-group ">
                    <input type="text" v-model="horas" @keypress="validaHoras" class="form-control" aria-describedby="curp" placeholder="HORAS">
                </div>
            </div>
            <div class="col-md-4 ">
                <div class="form-group ">
                    <input type="text" v-model="consecutivo" @keypress="validaConsecutivo" class="form-control" aria-describedby="curp"
                        placeholder="CONSECUTIVO">
                </div>
            </div>
        </div>

        <div class="sec_plaza">
            <div class="row">
                <div class="col">
                    <span>Seccion Plaza : <span class="plaza">
                            {{seccion==='SECCION' ? '-' : seccion}}
                        </span></span>
                </div>
            </div>
            <div class="row">
                <div class="col">
                    <span>Codigo Plaza : <span class="plaza">{{codigo}} {{_codigo}} {{categoria}} {{horas}}
                            {{consecutivo}}</span></span>
                </div>
            </div>
            <div class="row" v-if="valida">
                <div class="col">
                    <span class="plaza error">{{msjError}} </span>
                </div>
            </div>
        </div>

        <div class="buttons ">
            <a class="btn primary " @click="muestraModal">
                Guardar
            </a>
            <a href=" " class="btn-outline primary ">
                Carga Masiva
            </a>
        </div>

    </div>
    <miModal titulo="PLAZA" mensaje="La plaza ha sido guardada" ></miModal>
    <myFooter></myFooter>

</template>

<script>

let seccion = 'SECCION'
let codigo = ''
let _codigo = '##########'
let categoria = null
let horas = ''
let consecutivo = ''
let valida = false
let msjError = ""


import { defineAsyncComponent } from 'vue'
const input_seccion = defineAsyncComponent(()=>import('@/modules/shared/inputs/seccion.vue'))
const miModal = defineAsyncComponent(()=>import('@/modules/shared/components/ModalAceptar.vue'))    
const myFooter = defineAsyncComponent(()=>import('@/modules/shared/components/Footer.vue'))
const tituloBusqueda = defineAsyncComponent(()=>import('@/modules/shared/components/TituloBusqueda.vue'))

export default({

    components:{
        input_seccion,
        miModal,
        myFooter,
        tituloBusqueda
    },
    
    data(){
        return{
            seccion,
            codigo,
            _codigo,
            categoria,
            horas,
            consecutivo,
            valida
        }
    },
    methods: {
        muestraModal(){
            $('#staticBackdrop2').modal('show')
        },
        limpia_codigo() {

            try {
                this._codigo = "";
                //let cuenta = this.codigo.length + this.categoria.length + this.horas.length + this.consecutivo.length
                //console.log(cuenta)
                /*if (cuenta > 20) {
                    this.valida = true
                    this.msjError = "El numero de caracteres de la PLAZA es muy grande"
                } else if (msjError < 20) {
                    this.valida = false

                }*/

            } catch {
                //console.log('err')
            }
        },
        validaCodigo(e) {
            if (this.codigo.length >= 6 || isNaN(parseInt(e.key)))
                e.preventDefault();
        },
        validaHoras(e) {
            if (this.horas.length >= 3 || isNaN(parseInt(e.key)))
                e.preventDefault();
        },
        validaConsecutivo(e) {
            if (this.consecutivo.length >= 6 || isNaN(parseInt(e.key)))
                e.preventDefault();
        }

    },
    watch:{
        
        codigo(){       this.limpia_codigo() },
        categoria(){    this.limpia_codigo() },
        horas(){        this.limpia_codigo() },
        consecutivo(){  this.limpia_codigo() }
        
    }
        
    
})
</script>


<style scope>
    .sec_plaza{
        margin: 15px;
        font-size: larger;
    }
    .plaza{
        font-weight:bold;
    }
    .error{
        color: red;
    }
</style>